export default {
	name: 'uni-number-box',
	props: {
		isMax: {
			type: Boolean,
			default: false
		},
		isMin: {
			type: Boolean,
			default: false
		},
		index: {
			type: Number,
			default: 0
		},
		value: {
			type: Number,
			default: 0
		},
		min: {
			type: Number,
			default: -Infinity
		},
		max: {
			type: Number,
			default: Infinity
		},
		step: {
			type: Number,
			default: 1
		},
		disabled: {
			type: Boolean,
			default: false
		}
	},
	
	data() {
		return {
			inputValue: this.value,
			minDisabled: false,
			maxDisabled: false
		}
	},
	created(){
		this.maxDisabled = this.isMax;
		this.minDisabled = this.isMin;
	},
	computed: {

	},
	watch: {
		inputValue(number) {
			console.log("number",number)
			const data = {
				number: number,
				index: this.index
			}
			this.$emit('eventChange', data);
		}
	},
	onShow(){
		
	},
	methods: {
		_calcValue(type) {
			const scale = this._getDecimalScale();
			let value = this.inputValue * scale;
			let newValue = 0;
			let step = this.step * scale;
			
			if(type === 'subtract'){
				newValue = value - step;
				if (newValue <= this.min){
					this.minDisabled = true;
				}
				if(newValue < this.min){
					newValue = this.min
				}
				if(newValue < this.max && this.maxDisabled === true){
					this.maxDisabled = false;
				}
			}else if(type === 'add'){
				newValue = value + step;
				if (newValue >= this.max){
					this.maxDisabled = true;
				}
				if(newValue > this.max){
					newValue = this.max
				}
				if(newValue > this.min && this.minDisabled === true){
					this.minDisabled = false;
				}
			}
			if(newValue === value){
				return;
			}
			this.inputValue = newValue / scale;
		},
		_getDecimalScale() {
			let scale = 1;
			// 浮点型
			if (~~this.step !== this.step) {
				scale = Math.pow(10, (this.step + '').split('.')[1].length);
			}
			return scale;
		},
		_onBlur(event) {
			let value = event.detail.value;
			if (!value) {
				this.inputValue = 0;
				return
			}
			value = +value;
			if (value >= this.max) {
				if(value > this.min && this.minDisabled === true){
					this.minDisabled = false;
				}
				if(this.maxDisabled === false){
					this.maxDisabled = true;
				}
				value = this.max;
			} else if (value <= this.min) {
				if(value < this.max && this.maxDisabled === true){
					this.maxDisabled = false;
				}
				if(this.minDisabled === false){
					this.minDisabled = true;
				}
				value = this.min
			}else {
				if(this.maxDisabled === true){
					this.maxDisabled = false;
				}
				if(this.minDisabled === true){
					this.minDisabled = false;
				}
			}
			this.inputValue = value
		}
	}
}
